package com.jonnyzzz.logo.web

import com.jonnyzzz.logo.web.components.ScrollToTopEffect
import com.jonnyzzz.logo.web.css.JogoStyle
import com.jonnyzzz.logo.web.i18.Message
import com.jonnyzzz.logo.web.i18.Messages
import com.jonnyzzz.logo.web.i18.msg
import com.jonnyzzz.logo.web.reducers.jogoProvider
import kotlinx.browser.document
import kotlinx.css.*
import react.FunctionComponent
import react.Props
import react.RBuilder
import react.dom.div
import react.dom.render
import react.router.dom.HashRouter
import react.router.dom.Route
import react.router.dom.Switch
import styled.css
import styled.styledDiv

private fun CssBuilder.mainComponentCss() {
  JogoStyle.html(this)

  child("div.main-content") {
    paddingLeft = 10.px
  }

  child("div.main-header") {
    fontSize = 200.pct
    fontWeight = FontWeight.bold
    paddingLeft = 10.px
  }
}

private fun RBuilder.defaultRoute(
  header: Message?,
  main: FunctionComponent<Props>
) {
  styledDiv {
    css.mainComponentCss()
    child(ScrollToTopEffect)

    div(classes = "main-menu") {
      child(MainMenuComponent)
    }

    div(classes = "main-header") {
      +Messages.Titles.jogo
      if (header != null) {
        +" / "
        msg(header)
      }
    }

    div(classes = "main-content") {
      child(main)
    }

    child(LogoFooter)
  }
}

fun main() {
  println("AAA")
  render(document.getElementById("root")) {
    styledDiv {
      css {
        display = Display.none
      }
      +"Made by @Jonnyzzz"
    }

    jogoProvider {
      HashRouter {
        Switch {
          Route {
            attrs.path = arrayOf("")
            attrs.exact = true
            defaultRoute(null, JogoStudio)
          }

          Route {
            attrs.path = arrayOf(JogoAppRoutes.helpPath)
            attrs.exact = true
            defaultRoute(JogoHelpHeader, JogoHelp)
          }

          Route {
            attrs.path = arrayOf(JogoAppRoutes.sharePath)
            attrs.exact = true
            defaultRoute(JogoShareHeader, JogoShare)
          }

          Route {
            attrs.path = arrayOf(JogoAppRoutes.sharePath + "*")
            defaultRoute(LogoShareAcceptHeader, LogoShareAccept)
          }

          Route {
            attrs.path = arrayOf("*")
            defaultRoute(NotFoundHeader, NotFound)
          }
        }
      }
    }
  }
}
