package com.jonnyzzz.logo.script

import kotlinx.coroutines.*
import kotlinx.coroutines.flow.MutableSharedFlow
import kotlinx.coroutines.flow.SharedFlow
import kotlinx.coroutines.flow.asSharedFlow
import kotlin.coroutines.CoroutineContext
import kotlin.math.PI
import kotlin.math.cos
import kotlin.math.sin

sealed class JSvgElement {
  abstract val penColor: PenColor
}

data class JSvgLineElement(
  val from: ScriptPoint,
  val to: ScriptPoint,
  override val penColor: PenColor,
) : JSvgElement()

data class JSvgCircleElement(
  val center: ScriptPoint,
  val radius: Int,
  override val penColor: PenColor,
) : JSvgElement()

data class JSvgPathElement(
  val d : String,
  override val penColor: PenColor,
) : JSvgElement()

data class WorldSnapshot(
  val world: ScriptWorld,

  val startState: ScriptState,
  val finalState: ScriptState,

  val svg: List<JSvgElement>,
)

class ReactiveWorldPlayer(
  private val world: ScriptWorld,
  private val initialState: ScriptState = ScriptState.ZERO,
  dispatcher : CoroutineDispatcher = Dispatchers.Main,
) {
  private val ourJob: Job = Job()

  private val scope = object : CoroutineScope {
    override val coroutineContext: CoroutineContext = dispatcher + ourJob + CoroutineName("World Player")
  }

  fun cancel() { ourJob.cancel() }

  private val internalChangesFlow = MutableSharedFlow<WorldSnapshot>()
  val changes : SharedFlow<WorldSnapshot> = internalChangesFlow.asSharedFlow()

  private val player get() = WorldPlayer(world, initialState)

  fun updateScript(script: Script) {
    println("ReactiveWorldPlayer = " + this.world)
    //cancel all pending animations or whatever is happening
    ourJob.cancelChildren()

    scope.launch {
      //make sure we are up-to-date
      yield()

      val commands = processScript(script)

      //make sure we can cancel
      yield()

      val scenes = player.play(commands)
      suspend fun playMe() {
        yield()
        for ((scene, action) in scenes) {
          yield()
          internalChangesFlow.emit(scene)
          yield()
          if (action == null) continue

          @Suppress("UNUSED_VARIABLE")
          val ignore = when (action) {
            is ScriptFrameDelayCommand -> delay(action.delayMillis.toLong())
            is ScriptReplayCommand -> {
              delay(300L)
              playMe()
              return
            }
          }
        }
      }

      playMe()
    }
  }
}

class WorldPlayer(
  private val world: ScriptWorld,
  private val initialState: ScriptState = ScriptState.ZERO,
) {
  private fun newSnapshot() = WorldSnapshot(world, initialState, initialState, listOf())

  fun play(
    parsedScript: ParsedScriptContext,
    start: WorldSnapshot = newSnapshot()
  ): List<Pair<WorldSnapshot, ScriptTemporalAction?>> = sequence {
    val svg = mutableListOf<JSvgElement>()
    var state = start.finalState

    suspend fun SequenceScope<Pair<WorldSnapshot, ScriptTemporalAction?>>.yieldWorld(temporal: ScriptTemporalAction? = null) {
      yield(WorldSnapshot(world, start.finalState, state, svg.toList()) to temporal)
    }

    for (scriptCommand in parsedScript.commands) {
      if (scriptCommand is ScriptTemporalAction) {
        yieldWorld(scriptCommand)
        continue
      }

      val newState = scriptCommand.updateState(state)

      if (state.penIsOn) {
        if (scriptCommand is ScriptArcCommand) {
          if (scriptCommand.angle < 360) {
            svg += toArcSector(
              state.point,
              scriptCommand.radius,
              state.headDirection,
              state.headDirection + scriptCommand.angle,
              newState.penColor
            )
          } else {
            svg += JSvgCircleElement(state.point, scriptCommand.radius, newState.penColor)
          }
        } else if (newState.point != state.point) {
          svg += JSvgLineElement(state.point, newState.point, newState.penColor)
        }
      }

      state = newState
    }

    yieldWorld()
  }.toList()
}


private fun toArcPoint(p : ScriptPoint,
                       radius : Int,
                       angleInDegrees: Int): ScriptPoint {
  val angleInRadians = angleInDegrees * PI / 180.0
  val x = p.x + radius * sin(angleInRadians)
  val y = p.y - radius * cos(angleInRadians)
  //we do not allow non-int coordinates for the jogo
  return ScriptPoint(x.toInt(), y.toInt())
}

private fun toArcSector(p : ScriptPoint,
                        radius : Int,
                        startAngle : Int,
                        endAngle : Int,
                        penColor: PenColor,
): JSvgPathElement {
  if (startAngle > endAngle) {
    return toArcSector(p, radius, endAngle, startAngle, penColor)
  }

  val start = toArcPoint(p, radius, endAngle)
  val end = toArcPoint(p, radius, startAngle)
  val largeArcFlag = if (endAngle - startAngle <= 180) "0" else "1"

  val d = listOf(
    "M", start.x, start.y,
    "A", radius, radius, 0, largeArcFlag, 0, end.x, end.y
  ).joinToString(" ")
  return JSvgPathElement(d, penColor)
}
