package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.script.*
import com.jonnyzzz.logo.web.css.JogoStyle.mainBorder
import com.jonnyzzz.logo.web.custom
import com.jonnyzzz.logo.web.reducers.useJogoSelector
import kotlinx.coroutines.*
import kotlinx.coroutines.flow.collect
import react.*
import styled.styledSvg
import kotlin.js.json

external interface SvgWorldProps : Props {
  var world: ScriptWorld
  var initialState: ScriptState?
  var script: Script
}

val SvgWorldMainComponent = functionComponent<Props> {
  val script = useJogoSelector { script }
  val world = useJogoSelector { world }

  child(SvgWorld) {
    attrs {
      this.script = Script(script)
      this.world = world
    }
  }
}

val SvgWorld = functionComponent<SvgWorldProps> { props ->
  val world = props.world
  val initialState = props.initialState ?: ScriptState.ZERO
  val (snapshot, setSnapshot) = useState<WorldSnapshot?>(null)

  rawUseEffect({
    val reactiveWorld = ReactiveWorldPlayer(world, initialState)
    val job = Job()

    val cleanup: () -> Unit = {
      job.cancel()
      reactiveWorld.cancel()
    }

    val scope = object : CoroutineScope {
      override val coroutineContext = Dispatchers.Main + job + CoroutineName("UI")
    }

    scope.launch {
      reactiveWorld.changes.collect {
        launch {
          setSnapshot(it)
        }
      }
    }

    reactiveWorld.updateScript(props.script)

    return@rawUseEffect cleanup
  }, listOf<dynamic>(props).toTypedArray())

  styledSvg {
    mainBorder(css)
    // https://developer.mozilla.org/en-US/docs/Web/SVG/Attribute/viewBox
    // minX, minY, width, height
    attrs["width"] = "100%"
    attrs["viewBox"] = world.toViewBox()

    val worldSnapshot = snapshot ?: return@styledSvg
    for (jSvg in worldSnapshot.svg) {
      toSvg(jSvg)
    }

    jogoHead(worldSnapshot)
  }
}

private fun RBuilder.jogoHead(state: WorldSnapshot) {
  jogoHead(state.finalState, state.world.tSize)
}

private fun RBuilder.jogoHead(pos: ScriptState, tSize: Int) {
  if (!pos.jogoIsVisible) return

  custom("circle") {
    attrs["cx"] = pos.x
    attrs["cy"] = pos.y
    attrs["r"] = tSize
    attrs["style"] = json(
      "stroke" to "black",
      "fill" to "red",
    )
  }

  custom("line") {
    attrs["x1"] = pos.x
    attrs["y1"] = pos.y
    attrs["x2"] = pos.x + tSize * pos.dX
    attrs["y2"] = pos.y + tSize * pos.dY
    attrs["strokeWidth"] = "2px"
    attrs["stroke"] = "black"
  }
}

private fun RBuilder.toSvg(svg: JSvgElement) {
  when (svg) {
    is JSvgLineElement -> toSvg(svg)
    is JSvgCircleElement -> toSvg(svg)
    is JSvgPathElement -> toSvg(svg)
    else -> error("Unknown element: $svg")
  }
}

private inline fun <T> inlineForEach(t1: T, t2: T, p: (T) -> Unit) { p(t1); p(t2) }

private fun RBuilder.toSvg(svg: JSvgLineElement) {
  custom("line") {
    attrs["x1"] = svg.from.x
    attrs["y1"] = svg.from.y
    attrs["x2"] = svg.to.x
    attrs["y2"] = svg.to.y
    attrs["stroke"] = svg.penColor.toSvgStroke()
    attrs["strokeWidth"] = /*if (script.hover) "4px" else*/ "2px"
  }

  inlineForEach(svg.from, svg.to) { p ->
    custom("circle") {
      attrs["r"] = "1"
      attrs["cx"] = p.x
      attrs["cy"] = p.y
      attrs["fill"] = svg.penColor.toSvgStroke()
    }
  }
}

private fun RBuilder.toSvg(svg: JSvgCircleElement) {
  custom("circle") {
    attrs["r"] = svg.radius
    attrs["cx"] = svg.center.x
    attrs["cy"] = svg.center.y
    attrs["fill"] = "none"
    attrs["stroke"] = svg.penColor.toSvgStroke()
    attrs["strokeWidth"] = /*if (script.hover) "4px" else*/ "2px"
  }
}

private fun RBuilder.toSvg(svg: JSvgPathElement) {
  custom("path") {
    attrs["d"] = svg.d
    attrs["fill"] = "none"
    attrs["stroke"] = svg.penColor.toSvgStroke()
    attrs["strokeWidth"] = /*if (script.hover) "4px" else*/ "2px"
  }
}

private fun PenColor.toSvgStroke(): String {
  return when (this) {
    PenColor.BLACK -> "black"
    is PenColor.RGB -> "rgb($r,$g,$b)"
  }
}
