package com.jonnyzzz.logo.web

import org.w3c.dom.events.Event
import react.router.dom.History
import react.router.dom.useHistory


object JogoAppRoutes {
  const val helpPath = "/help"
  const val sharePath = "/share"
  const val studioPath = "/"
}

class JogoHistory(
  private val history: History
) {
  fun isHelpPage() = history.location.pathname.startsWith(JogoAppRoutes.helpPath)

  fun isStudio() = history.location.pathname.trim('/').isBlank()
  fun isSharePage() = history.location.pathname.startsWith(JogoAppRoutes.sharePath)

  fun pushShare(it: Event? = null) {
    it?.preventDefault()
    history.push(JogoAppRoutes.sharePath)
  }

  fun pushHelp(it: Event? = null) {
    it?.preventDefault()
    history.push(JogoAppRoutes.helpPath)
  }

  fun pushStudio(it: Event? = null) {
    it?.preventDefault()
    history.push(JogoAppRoutes.studioPath)
  }

  fun pushBack(it: Event? = null) {
    it?.preventDefault()
    history.goBack()
  }
}

fun useJogoHistory() = JogoHistory(useHistory())

